import { Character, Image } from './../../model/character';
import { EngineService } from './../../core/engine/engine.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';


@Component({
  selector: 'my-hero-detail',
  templateUrl: './character.component.html',
  styleUrls: ['./character.component.css']
})
export class CharacterDetailComponent implements OnInit {
  error: any;
  character: Character;
  comics: any;
  constructor(
    private engine: EngineService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.route.params.forEach((params: Params) => {
      if (params['id'] !== undefined) {
        const id = params['id'];
        this.engine.getCharacter(id).subscribe(res => {
          if (res) {
            this.character = res.data.results[0];
            this.engine.getComicsOfCharacter(id).subscribe(res => {
              if (res) {
                this.comics = res.data.results;
              }
            });
          }
        });
      } else {
        this.character = new Character();
      }
    });
  }

  getImageUrl(image: Image) {
    const { path, extension } = image;
    return `${path}.${extension}`;
  }

  goBack(): void {
    window.history.back();
  }
}
