import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Character, Image } from '../../model/character';
import { EngineService } from 'app/core/engine/engine.service';


@Component({
  selector: 'my-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  characters: Character[] = [];
  selectedCharacter: Character;
  error: any;
  constructor(
    private router: Router,
    private engine: EngineService) {
  }

  ngOnInit(): void {
    const request = {limit: 100};
    this.engine.getCharacters(request)
      .subscribe(res => {
        if (res) {
          this.characters = res.data.results;
        }
      }, error => (this.error = error));
  }

  gotoDetail(c: Character): void {
    const link = ['/detail', c.id];
    this.router.navigate(link);
  }

  getImageUrl(image: Image) {
    const { path, extension } = image;
    return `${path}.${extension}`;
  }

  getLimitedString(str: string, num: number = 100) {
    return str.length > num ? str.substring(0, num - 1) + '...' : str;
  }
}
